import styled from 'styled-components'

export default styled.div`
  border-radius: 2px;
  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.12), 0 1px 4px rgba(0, 0, 0, 0.12);
  background-color: #ffffff;
  padding: 20px;
  display:flex;
  flex-flow:column;
`


